.card {
    position: relative;
    background-color:white;
    border-left: 0.25em solid #fcbf1e;
    border-right: 0.25em solid #40bad5;
}

.card-options {
    height:50px;
    width: 100%;
    overflow: hidden;
    opacity: 1;
}

.default-content {
    overflow: hidden;
    overflow-wrap: break-word;
    transition: 0.5s ease;
} 

.hover-content {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.card:hover{
    opacity:1;
    border-left: 0.25em solid #40bad5;
    border-right: 0.25em solid #fcbf1e;
    transition: .5s linear;
}

.card:hover .default-content{
    color:white;
    background-color: #120136;
    transition: 0.5s ease;
}

.card:hover .hover-content{
    opacity:1;
    transition: .5s ease;
}

.card-dimensions{
    height: 200px;
    width: 300px;
}

.max-dimensions {
    height: 100%;
    width: 100%;
}

.info-dimensions {
    height:75%;
    width: 100%;
}

.background {
    opacity: 0.7;
    background-color: #120136;
}

