.add-modal-background {
    background-color: #120136af;
}
.view-modal-background {
    background-color: #36c032af;
}
.edit-modal-background {
    background-color: #3682d8af;
}
.delete-modal-background {
    background-color: #dd3d37af;
}

.modal {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    position:relative;
    background-color:white;
    border-left: 0.25em solid #fcbf1e;
    border-right: 0.25em solid #40bad5;
    border-radius: 2rem;
}

.modal-dimensions{
    height: 50vh;
    width: 50vw;
}

.modal-options {
    height: 50px;
    width: 100%;
    overflow: hidden;
    opacity: 1;
}

.w-25 {
    height: 100%;
    width: 25%;
}