/* --------- */
/* CONTAINER */
/* --------- */
.df {
    display: flex;
}
.dg {
    display: grid
}
.di {
    display: inline;
}

/*flex-direction*/
.f-d-row {
    flex-direction: row;
}
.f-d-column {
    flex-direction: column;
}

/*flex-wrap*/
.f-w-nowrap {
    flex-wrap: nowrap;
}
.f-w-wrap {
    flex-wrap: wrap;
}

/*justify-content*/
.f-j-c-start {
    justify-content: flex-start;
}
.f-j-c-end {
    justify-content: flex-end;
}
.f-j-c-center {
    justify-content: center;
}
.f-j-c-space-between {
    justify-content: space-between;
}
.f-j-c-space-around {
    justify-content: space-around;
}

/*align-items*/
.f-a-i-start {
    align-items: flex-start;
}
.f-a-i-end {
    align-items: flex-end;
}
.f-a-i-center {
    align-items: center;
}
.f-a-i-stretch {
    align-items: stretch;
}

/*align-content*/
.f-a-c-start {
    align-content: flex-start;
}
.f-a-c-end {
    align-content: flex-end;
}
.f-a-c-center {
    align-content: center;
}
.f-a-c-space-around {
    align-content: space-around;
}
.f-a-c-space-between {
    align-content: space-between;
}
.f-a-c-stretch {
    align-content: stretch;
}
/* ------- */
/* CONTENT */
/* ------- */
.f-a-s-start {
    align-self: flex-start;
}
.f-a-s-end {
    align-self: flex-end;
}
.f-a-s-center {
    align-self: center;
}
.f-a-s-stretch {
    align-self: stretch;
}

.j-s-center {
    justify-self: center;
}
.j-s-self-start {
    justify-self: self-start;
}
.j-s-self-end {
    justify-self: self-end;
}
.f-j-s-start {
    justify-self: flex-start;
}
.f-j-s-end {
    justify-self: flex-end;
}
.f-f-b-170px { 
    flex-basis:100px;
}
.f-f-b-40p { 
    flex-basis:40%;
}

.min-h-100 {
    min-height: 100%;
}
.h-100{
    height:100%;
}

.f-f-g-1{
    flex-grow: 1;
}
.f-f-g-2{
    flex-grow: 2;
}
.f-f-g-3{
    flex-grow: 3;
}
.f-f-g-4{
    flex-grow: 4;
}

.max-dimensions {
    height: 100%;
    width: 100%;
}

.relative {
    position: relative;
}
.absolute {
    position: absolute;
}

.overlay {
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;

}

.overflow-hidden {
    overflow: hidden;
}