.start-btn {
    text-decoration: none;
    color: #fcbf1e;
    background-color: transparent;
    border: 2px solid #fcbf1e;
    border-radius: 1rem;
    margin: 0 auto;
    padding: 1rem;
    font-weight: 600;
    outline:none;
}

.start-btn:hover {
    background-color: #fcbf1e;
    color:inherit;
    border-color:inherit;
}

.start-btn:active {
    color:white;
    border-color:white;
}

.add-btn {
    height:50px;
    width: 200px;
    background-color: #120136;
}
.view-btn {
    background-color: #36c032;
}
.edit-btn {
    background-color: #3682d8;
}
.delete-btn {
    background-color: #dd3d37;
}
.save-btn {
    background-color: #3682d8;
    height:50px;
    width: 100%;
}
.cancel-btn {
    background-color: #6d789b;
    height:50px;
    width: 100%;
}


.options-btn {
    text-decoration: none;
    color: white;
    border: 0px solid white;
    margin: 0;
    font-weight: 600;
    outline:none;
}

.options-btn:hover {
    border-bottom: 0.25em solid #fcbf1e;
    transition: .5s ease;
}